.reviewContainer {
    max-width: 580px;
    margin-top: 15px;
    margin-bottom: 15px;
    border-bottom: 1px solid grey;
    padding: 10px;
}

.authorContainer {
    display: flex;
    flex-direction: column;
    gap: 10px;
}

.author {
    display: flex;
    gap: 15px;
    font-size: 24px;
    color: black;
    margin-bottom: 5px;
}

.authorName {
    font-weight: 800;
}

.ContentTitle {
    font-size: 24px;
    font-weight: 700;
    margin-top: 15px;
    margin-bottom: 15px;
}

.Content {
    color: rgb(44, 43, 43);
    font-style: italic;
}